import React, { useState, useEffect } from 'react';
import './DonatePage.css';

declare global {
  interface Window {
    paypal: any;
  }
}

const DonatePage = () => {
  const [selectedAmount, setSelectedAmount] = useState(25);
  const [customAmount, setCustomAmount] = useState('');
  const [isPayPalScriptLoaded, setIsPayPalScriptLoaded] = useState(false);

  const handleAmountSelect = (amount: number) => {
    setSelectedAmount(amount);
    setCustomAmount('');
  };

  const handleCustomAmountChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;
    setCustomAmount(value);
    if (value) {
      setSelectedAmount(Number(value));
    }
  };

  // 1) Load the PayPal script once on mount
  useEffect(() => {
    const script = document.createElement('script');
    script.src =
      'https://www.paypal.com/sdk/js?client-id=AfzGnAvOAMSeL1uEztJu8EuuarJEJzTLXZ7g-MwsPCSys15CeZOIFRJz_T09Otz_MqrxBdMBv2gBaDQ4&currency=USD';
    script.async = true;

    script.onload = () => {
      setIsPayPalScriptLoaded(true);
    };

    document.body.appendChild(script);

    // Optional cleanup if you want to remove the script on unmount
    return () => {
      if (document.body.contains(script)) {
        document.body.removeChild(script);
      }
    };
  }, []);

  // 2) Render (or re-render) the PayPal button whenever script loads OR the amount changes
  useEffect(() => {
    if (isPayPalScriptLoaded && window.paypal) {
      // Clear out old buttons to avoid duplication
      const container = document.getElementById('paypal-button-container');
      if (container) {
        container.innerHTML = '';
      }

      window.paypal
        .Buttons({
          createOrder: (_data: any, actions: any) => {
            return actions.order.create({
              purchase_units: [
                {
                  amount: {
                    value: selectedAmount.toString(),
                  },
                },
              ],
              application_context: {
                shipping_preference: 'NO_SHIPPING', // Removes shipping fields
              },
            });
          },
          onApprove: (_data: any, actions: any) => {
            return actions.order.capture().then((details: any) => {
              // Redirect to thank you page with hash router
              window.location.hash = '#/thank-you';
            });
          },
        })
        .render('#paypal-button-container');
    }
  }, [isPayPalScriptLoaded, selectedAmount]);

  return (
    <section className="donation-section no-zoom">
      <div className="container">
        <div className="row justify-content-center">
          <div className="col-md-8">
            <div className="donation-card">
              <div className="text-center mb-5">
                <h1 className="display-4 mb-3">Support Our Campaign</h1>
                <p className="lead">
                  Help us build a stronger Madison Township. Your contribution makes a difference.
                </p>
              </div>

              <div className="donation-options mb-5">
                <h3 className="text-center mb-4">Select Amount</h3>
                <div className="row g-3 justify-content-center mb-4">
                  {[25, 50, 100, 250].map((amount) => (
                    <div className="col-6 col-md-3" key={amount}>
                      <button
                        className={`btn ${
                          selectedAmount === amount && !customAmount
                            ? 'btn-primary'
                            : 'btn-outline-primary'
                        } w-100 py-3`}
                        onClick={() => handleAmountSelect(amount)}
                      >
                        ${amount}
                      </button>
                    </div>
                  ))}
                </div>

                <div className="custom-amount mb-4">
                  <label className="form-label">Or enter custom amount:</label>
                  <div className="input-group">
                    <span className="input-group-text">$</span>
                    <input
                      type="number"
                      className="form-control"
                      value={customAmount}
                      onChange={handleCustomAmountChange}
                      placeholder="Enter amount"
                      min="1"
                    />
                  </div>
                </div>

                {/* PayPal Button Container */}
                {!isPayPalScriptLoaded && (
                  <p className="text-center">Loading PayPal donation buttonâ€¦</p>
                )}
                <div id="paypal-button-container" className="mt-4"></div>
              </div>

              <div className="donation-info mt-5">
                <div className="alert alert-info">
                  <h4 className="alert-heading">Important Information</h4>
                  <ul className="mb-0">
                    <li>Contributions are not tax deductible</li>
                    <li>Campaign finance laws require us to collect your information</li>
                    <li>Maximum contribution limits apply</li>
                  </ul>
                </div>
                <p className="text-center mt-4">
                  <small>Paid for by Friends of Andrew B. Nimely</small>
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default DonatePage;
